
@import url('https://fonts.googleapis.com/css2?family=Freckle+Face&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Spartan:wght@200;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Arvo:ital@1&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,300;0,400;1,300&display=swap');
/* FONTS */
$Freckle: "Freckle Face", sans-serif;
$Bebas: "Bebas Neue", sans-serif;
$Spartan: "Spartan", serif;
$Arvo: "Arvo", serif;
