  }

.main-landing-img{
  background-image: url("/images/landing-img.jpg");
  position: absolute;
  width: 100%;
  background-size: cover;
  max-height: 100vh;
  z-index: -1;
}

// // ###################################  landing page 
.landing-over-img{
  font-family:"Arvo";
  color: $lemon-meringue;
  text-shadow: $lemon-shadow;
  margin: 11vw 0 10vw 5vw;
  font-size: 2.5vw;

