// END LANDING-OVER-IMG 
.landing-content{
    background-color:white;
    color:$prussian-blue;
    font-family:"Spartan";
  
  .content-row{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    grid-column-gap:5vw;
    padding-top: 20px;
    height:33vw;
    margin: 1em;
  }
  
  .content-text{
    font-weight:bold;
    font-size: 2vw;
    white-space: wrap;
  }
}

.hero-btn-form{
  margin: 100% 0 0 20%;
}

.content-btn-form{
  margin: auto;
  max-width: 45vw;
  padding:0.15em;
}

@mixin std-btn {
  background: transparent;
  letter-spacing: 2px;
  text-transform: uppercase;
  cursor: pointer;
  display: inline-block;
  -webkit-transition: all 0.4s;
  -moz-transition: all 0.4s;
  transition: all 0.4s;

}

// thanks to https://codepen.io/forkintheroad/pen/xxGaXbK 
button.landing-btn{
  @include std-btn;
  font-size: 1.25rem;
  padding: 1rem 3rem;
  color: $lemon-meringue;
  border: 2px solid $lemon-meringue;
  box-shadow: 2px 2px 2px $maximum-yellow-red;
}

button.content-btn{
  @include std-btn;
  font-size: 2.7vw;
  padding: 1em;
  color: $prussian-blue;
  border: 2px solid $prussian-blue;
  box-shadow: 2px 2px 2px $prussian-blue;
}

@media only screen and (max-width: 1000px){
  button.landing-btn{
     display: none;
  }
}

button.lb-2{
  overflow: hidden;
  position: relative;
}

button.lb-2:hover{
  color: $prussian-blue;
}

button.lb-2:after {
  content:'';
  position: absolute;
  z-index: -1;
  -webkit-transition: all 0.4s;
  -moz-transition: all 0.4s;
  transition: all 0.4s;
  width: 100%;
  height: 0;
  top: 0;
  left: 0;
  background: $lemon-meringue;
}

button.lb-2:hover:after,
button.lb-2:active:after {
  height: 100%;
}

// // ################### search bar styling 
// //  Thanks to https://codepen.io/takaneichinose/pen/gKVXXL
.search {
  margin: 2vw; 
}

.search > div {
  display: inline-block;
  position: relative;
}

.search > div:after {
  content: "";
  background: $maximum-red;
  width: 5px;
  height: 2.3vh;
  position: absolute;
  top: 5.3vh;
  right: .4vh;
  // left:
  box-shadow: 1px 2px 5px $prussian-blue;
  transform: rotate(140deg);
}

.search > div > input {
  color: $prussian-blue;
  font-size: 2.3vw;
  background: transparent;
  width: 2.2vh;
  height: 2.2vh;
  padding: 0.625rem;
  border: solid 3px $maximum-red;
  box-shadow: 1px 2px 5px $prussian-blue;

  outline: none;
  border-radius: 35px;
  transition: width 0.5s;
}

.search > div > input::placeholder {
  color:$prussian-blue;
  opacity: 0;
  transition: opacity 150ms ease-out;
}

.search > div > input:focus::placeholder {
  opacity: 1;
}

.search > div > input:focus,
.search > div > input:not(:placeholder-shown) {
  width: 30vw;
}

@media screen and (max-width:630px){
  .search > div > input:focus,
  .search > div > input:not(:placeholder-shown) {
    width: 20vw;
  }
}

@media screen and (max-width:630px){
  .search > div > input:focus,
  .search > div > input:not(:placeholder-shown) {
    width: 20vw;
  }
}

// // #################################### MODAL STYLING 
.modal{
  position: fixed;
  padding-top: 25px;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 3;
  background-color: rgba(0, 0, 0, 0.97);
  -webkit-transition: 0.5s;
  transition: all 0.3s linear;
  overflow: auto;
  text-align: center;

  a{
    text-decoration: none;
    color: $lemon-meringue;
    font-size: 2vw;
    h2, h3, h4{
      margin: 0;
    }
  }

  
  button{
    margin-left: 36%;
    background: $grayish;
    border-radius: 50%;
    color: $maximum-red;
    font-weight: bold;  
  }
}  

// // ######################## CONTENT LIST  
.content-header{
  display: grid;
  grid-template-columns: 33% auto;
  grid-template-rows: 10vw ;
  grid-column-gap: 4vw;
  font-family: "Bebas Neue";
  letter-spacing: 2px;

  .search-bar{
    align-self: center;
  }
  .title{
    align-self: center;
    font-size: 3vw;
  }

}



// // ####################### BROWSE BY LETTER 
.letters{
  font-size:3vw;
  font-family: "Bebas Neue";
  margin: auto;
  color: $prussian-blue;
  border-bottom: 2px solid $maximum-red;
}

// // ###########################################  LIST CARDS
.list-container{
  margin: 1vw 2vw 5vh 2vw;
}

.list-cards{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: flex-start;
}

.list-card{
  margin-top: 5vw;
  background:$grayish;
  flex: 0 1 calc(25% - 1.5em);
  font-family: "Merriweather";
  border: 1px inset $prussian-blue;
  border-radius: 5px;
  box-shadow: 2px 5px 10px $prussian-blue;

  a{
    display: block;
    text-align: center;
    text-decoration: none;
    color: black;
    text-align: center;
    cursor: pointer;

    &:hover {
      filter: alpha(opacity=30);
      color: $prussian-blue;
      box-shadow: 0px 10px 50px $grayish;
    }

    img{
      margin-top: 3px;
      border: 3px inset $prussian-blue;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      overflow:hidden;
    }

    /* Shine */
  figure {
    position: relative;
    overflow: hidden;
  }


  figure::before {
    position: absolute;
    top: 0;
    left: -65%;
    z-index: 2;
    display: block;
    content: '';
    width: 47%;
    height: 100%;
    background: -webkit-linear-gradient(left, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
    background: linear-gradient(to right, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
    -webkit-transform: skewX(-25deg);
    transform: skewX(-25deg);
  }

  figure:hover::before {
    -webkit-animation: shine .75s;
    animation: shine .75s;
  }
  @-webkit-keyframes shine {
    100% {
      left: 100%;
    }
  }
  @keyframes shine {
    100% {
      left: 100%;
    }
  }

    
}
  
  
  h3{
    text-transform: uppercase;
    font-weight: 300;
  }
  h4{
    text-transform: full-size;
    font-weight: 700;
  }
}

@media screen and (max-width:432px){
    .list-cards{
      justify-content: center;
    }
}

