//  ######################## LOADING ICON THANKS TO https://loading.io/css/
  
.lds-hourglass {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  position: absolute;
left: 50%;
top: 50%;
transform: translate(-50%, -50%);

}
.lds-hourglass:after {
  content: " ";
  display: block;
  border-radius: 50%;
  width: 10vw;
  height: 10vw;
  text-align: center;
  margin: 8px;
  box-sizing: border-box;
  border: 32px solid $maximum-yellow-red;
  border-color: $maximum-red transparent $maximum-red transparent;
  animation: lds-hourglass 1.2s infinite;
}
@keyframes lds-hourglass {
  0% {
    transform: rotate(0);
    animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
  50% {
    transform: rotate(900deg);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  100% {
    transform: rotate(1800deg);
  }
}
